
#servicios{
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  background: white;
  padding: 2rem;
}

#servicios hr {
  width: 100%;
}

#servicios h3 {
  font-size: 3rem;
}

#servicios-grid {
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  gap: 1.2rem;
  background: white;
  padding: 2rem;
}

.servicio-caja {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-around;
  gap: 1rem;
  width: 10rem;
  border-radius: 10px;
  padding: 1rem;
  background: #00348f;
  transition: .2s;
  text-decoration: none;
}

.servicio-icono {
  width: 7rem;
  filter: invert(100%) url(#color-filter);
}

.servicio-caja:hover{
  transform: scale(1.05);
  background: #004ed3;
}

.servicio-caja p {
  text-align: center;
  text-decoration: none;
  color: white;
  font-weight: 800;
}

@media screen and (max-width: 680px) {
  .servicio-caja {
    width: 9.7rem;
  }

  #servicios-grid {
    padding: 1rem 0;
    gap: .9rem;
  }

  #servicios {
    padding: 1rem .8rem;
  }
}
